*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
  color: #383838;
  font-size: 14px;
}

.container{
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.header{
  height: 60px;
  position: sticky;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  border-bottom: 0.5px solid rgb(219,219,219);
}

.logo{
  width: 131px;
}

.buttons{
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
  width: 55%;
  height: 30px;
}

.buttons .btn{
  margin-left: 16px;
}

.btn{
  color: #0095F6;
  border: none;
  background-color: transparent;
  font-size: 14px;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;
}

.btn.transparent{
  border: 0.5px solid rgb(219,219,219);
  color:#383838;
  padding: 5px 9px;
}


.btn.blue{
  background-color: #0095F6;
  color: white;
  padding: 5px 9px
}

.large{
  margin-left: 4px;
  flex-grow: 1;
}

.card{
  display: flex;
  flex-direction: column;
  border-bottom: 0.5px solid rgb(219,219,219);
}

.infos{
  display: flex;
  margin: 16px 16px 24px 16px;
  justify-content: space-between;
}

.avatar{
  width: 91px;
  height: 91px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 28px;
  background-image: url(./images/avatar.jpg);
  background-position: center;
  background-size: 77px 77px;
  background-repeat: no-repeat;
  clip-path: circle(40%);
}


.naming{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.naming-buttons{
  display: flex;
}

.name-infos{
  display: flex;
  align-items: center;
}

.name{
  font-size: 28px;
  font-weight: 300;
}

.certified{
  height: 20px;
  width: 20px;
  margin-left: 8px;
}

.dots{
  height: 40px;
  width: 40px;
  margin-left: 5px;
  padding: 8px;
}


.description{
  padding: 0 16px 21px 16px;
  display: flex;
  flex-direction: column;
}

.important{
  font-weight: 600;
  font-size: 14px;
}

.account-stats{
  padding:12px 0px;
  border-bottom: 0.5px solid rgb(219,219,219);
  display: flex;
  justify-content: space-around;
}

.stat{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.number{
  font-weight: 600;
}

.stat-name{
  color :rgb(142,142,142)
}

.selector img{
  width: 25px;
  height: 25px;
}

.selector{
  display: flex;
  height: 44px;
  justify-content: space-around;
}

.selection{
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}


.publications{
  display: flex;
  width: 100vw;
  flex-grow: 1;
  flex-direction: column;
  overflow-y: scroll;
}

.row{
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 0.5%;
  height: 30%;
}

.publication{
  width: 33%;
  aspect-ratio: 1/1;
  cursor: pointer;
}


@media screen and (min-width: 1024px ) {
  .container >*{
    width: 80%;
  }
}